"use client"; // Required for hooks in Next.js App Router

import { useState } from "react";

export default function Register() {
  // We keep track of the username, password, and department
  const [form, setForm] = useState({
    username: "",
    password: "",
    department: "",
  });

  const handleRegister = async () => {
    try {
      // Points to your Express server (check your port, usually 3001 or 5000)
      const res = await fetch("http://localhost:3001/api/register", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(form),
      });

      if (!res.ok) {
        const msg = await res.text();
        alert(`Registration failed: ${msg}`);
        return;
      }
      const data = await res.json();
      alert(`Registered as ${data.role}!`);
    } catch (error) {
      console.error("Registration failed:", error);
    }
  };

  return (
    <div style={{ padding: "20px" }}>
      <h1>Register</h1>

      {/* Input for Username */}
      <input
        placeholder="Username"
        onChange={(e) => setForm({ ...form, username: e.target.value })}
        style={{ display: "block", marginBottom: "10px" }}
      />

      {/* Input for Password */}
      <input
        type="password"
        placeholder="Password"
        onChange={(e) => setForm({ ...form, password: e.target.value })}
        style={{ display: "block", marginBottom: "10px" }}
      />

      {/* Input for Department - This triggers the 'Head' logic on the server */}
      <input
        placeholder="Department (e.g. IT, GASO)"
        onChange={(e) => setForm({ ...form, department: e.target.value })}
        style={{ display: "block", marginBottom: "10px" }}
      />

      <button onClick={handleRegister}>Register</button>
    </div>
  );
}
